.home {
    height: 100vh;
    width: 100vw;
    position: absolute;
}
.home:after {
    content: '';
    height: 900px;
    width: 900px;
    background: #e7f7fe;
    border-radius: 50%;
    position: absolute;
    bottom: -480px;
    right: 0px;
    z-index: -2;
    animation: homeAnim 15s infinite alternate;
}
@media screen and (max-width: 650px) {
    .home:after {
        height: 400px;
        width: 400px;
        bottom: -270px;
   }
}
@keyframes homeAnim {
    50% {
        transform: scale(0.8);
   }
    100% {
        transform: scale(1.1);
   }
}
@media screen and (max-width: 430px) {
    .home:after {
        display: none;
   }
}
.home .home-main {
    display: grid;
    margin:200px;
    align-items: center;
    justify-items: center;
    padding-left: 1rem;
    width: auto;
    height: 95%;
    overflow: hidden;
}
.home .home-main .main-content {
    user-select: none;
}
.home .home-main .main-content h1 {
    font-size: 3.7rem;
    position: relative;
    font-family: #00c1ec;
    color: #282828;
}
@media screen and (max-width: 650px) {
    .home .home-main .main-content h1 {
        font-size: 2rem;
   }
}
@media screen and (min-width: 1200px) {
    .home .home-main .main-content h1 {
        font-size: 5rem;
   }
}
.home .home-main .main-content h1:before {
    content: '';
    position: absolute;
    height: 150px;
    width: 150px;
    border-radius: 50%;
    border: 1px solid #00c1ec;
    top: 20px;
    left: -100px;
    z-index: -1;
    animation: h1Anim 1.5s forwards ease;
    transform: scale(0);
}
@media screen and (max-width: 850px) {
    .home .home-main .main-content h1:before {
        height: 110px;
        width: 110px;
        left: -40px;
        top: -15px;
   }
}
@keyframes h1Anim {
    to {
        transform: scale(1);
   }
}
.home .home-main .main-content h2 {
    font-size: 4.2rem;
    font-weight: 100;
}
@media screen and (max-width: 650px) {
    .home .home-main .main-content h2 {
        font-size: 3rem;
   }
}
@media screen and (min-width: 1200px) {
    .home .home-main .main-content h2 {
        font-size: 5rem;
   }
}