.dynamic-text {
  position: relative;
  display: flex;
}
.dynamic-text .simply {
  color: #8d8d8d;
}
.dynamic-text #text-target {
  display: flex;
  width: 300px;
  text-align: left;
  animation: color 30s alternate infinite;
  perspective: 200px;
  transform: translateX(26px);
}
.dynamic-text .word {
  position: absolute;
}
@keyframes color {
  0% {
    color: #00c1ec;
 }
  50% {
    color: #8d8d8d;
 }
  100% {
    color: #00c1ec;
 }
}
.dynamic-text .letter {
  transform: translateZ(200px);
}
@keyframes anim {
  25% {
    transform: translateZ(0px);
    opacity: 1;
 }
  32% {
    opacity: 0;
    transform: translateZ(-280px) translateX(200px);
 }
}
