.navigation {
    position: absolute;
    top: 1rem;
    left: 1.4rem;
    font-size: 1.4rem;
    line-height: 28px;
    color: black;
}
  
.navigation:before {
      content: '';
      position: absolute;
      height: 320px;
      width: 320px;
      border-radius: 50%;
      background:rgba(0, 0, 255, 0.541);
      top: -150px;
      left: -160px;
      z-index: -1;
      animation: navAnim 15s infinite alternate;
}
  
      @keyframes navAnim {
        50% {
          transform: scale(1.3);
        }
        100% {
          transform: scale(1);
        }
      }
      
    
    .nav-active {
      color: black;
    }
    ul:hover li:not(:hover) {
      opacity: 50%;
    }
    ul:hover li:hover {
      color: black;
    }
    
.nav-portfolio {
  cursor: pointer;
  display: flex;
  padding-right: 6rem;
  transition: .5s;
}
.nav-portfolio:hover .nav-projects,
.nav-projects:hover {

    visibility: visible;
    opacity: 1;
}
  
li {
  transform: rotatey(0deg)
}

.nav-projects {
position: absolute;
visibility: hidden;
opacity: 0;
padding: 0 40px;
width: 300px;
transform: translateX(70px);
transition: .3s ;
perspective: 500px;
}
  
li {
transform: rotatey(30deg) scaleX(1.2);
transform-origin: center;
transition: .3s;
}


